<template>
	<uni-popup ref="popup" :type="type" :is-mask-click="isMaskClick" :animation="animation"
		:mask-background-color="maskBackgroundColor" :background-color="backgroundColor" :border-radius="borderRadius"
		:safe-area="safeArea" @change="onChange" @maskClick="maskClick">
		<slot />
	</uni-popup>
</template>

<script>
	/**
	 * popup 弹窗
	 * @property {Boolean} show = [true|false] - 是否显示弹窗
	 * @property {Boolean} animation = [true|false] - 是否开启动画
	 * @property {String} type = [top|bottom|center] - 弹窗类型
	 * @property {Boolean} isMaskClick = [true|false] - 点击遮罩是否关闭弹窗
	 * @property {Boolean} maskBackgroundColor = [true|false] - 是否显示遮罩背景色
	 * @property {String} backgroundColor - 弹窗背景色
	 * @property {String} borderRadius - 弹窗圆角
	 * @property {Boolean} safeArea = [true|false] - 是否开启安全区域适配
	 * @event {Function} change - 弹窗状态改变时触发
	 * @example
	 * <oz-popup v-model="show" @change="change">
	 * 	<view>弹窗内容</view>
	 * </oz-popup>
	 */
	export default {
		name: "ozPopup",
		model: {
			prop: 'visible',
			event: 'update:visible',
		},
		props: {
			visible: {
				type: Boolean,
				default: false,
			},
			animation: {
				type: Boolean,
				default: true,
			},
			type: {
				type: String,
				default: 'center',
			},
			isMaskClick: {
				type: Boolean,
				default: true,
			},
			maskBackgroundColor: {
				type: String,
				default: 'rgba(0,0,0,0.4)',
			},
			backgroundColor: {
				type: String,
				default: 'none',
			},
			borderRadius: {
				type: String,
				default: '',
			},
			safeArea: {
				type: Boolean,
				default: true,
			},
		},
		data() {
			return {};
		},
		watch: {
			visible: {
				handler(val) {
					this.$nextTick(() => {
						if (val) {
							this.open();
						} else {
							this.close();
						}
					});
				},
				deep: true,
			},
		},
		mounted() {},
		methods: {
			open(data) {
				this.$refs.popup.open();
			},
			close() {
				this.$refs.popup.close();
			},
			onChange(e) {
				this.$emit('update:visible', e.show);
				this.$emit('change', e.show);
			},
			maskClick() {
				this.$emit('maskClick');
			},
		}
	}
</script>

<style lang="scss" scoped></style>